.skyButton {
  position: absolute;
  top: 10%;
  right: 10%;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  background-color: var(--red-300);
  z-index: 2;
  transition: 0.1s;
  filter: saturate(0);
  outline: 0.1rem solid var(--transp);
}

.skyButton:hover {
  outline: 0.1rem solid var(--light);
}

.cloudyIcon {
  background: url("../assets/images/cloudyIcon.webp");
  background-size: cover;
}

.clearIcon {
  background: url("../assets/images/clearIcon.webp");
  background-size: cover;
}
